@use "base/variables" as v;
@use "base/mixins" as m;

.esthetics-options-bg {
    .esthetics-title {
      position: relative;
      display: flex;
      justify-content: center;
      h2 {
        font-family: v.$title;
        position: absolute;
        text-transform: uppercase;
        color: v.$primaryBlueDark;
        font-size: 3rem;
        top: -2rem;
        text-align: center;
        @include m.tablet {
          font-size: 4rem;
          top: -2.5rem;
        }
        // @include m.laptop {
        //   font-size: 6rem;
        //   top: -3.7rem;
        // }
        @include m.desktop {
          font-size: 8rem;
          top: -5rem;
        }
        @include m.desktopXL {
          font-size: 10rem;
          top: -6rem;
        }
      }
    }
  }

.esthetics-options {
    margin: -2rem 2rem 10rem 2rem;
    .esthetics-options-grid {
      display: flex;
      justify-content: space-between;
      .esthetics-options-img {
        width: 100%;
        img {
          width: 100%;
          padding: 3rem;
          padding-bottom: 0;
        }
      }
      .esthetics-options-bg {
        width: 100%;
        height: 3rem;
        background-color: v.$primaryBlueDark;
        margin-top: 1rem;
        position: relative;
        .esthetics-title {
          position: absolute;
          width: 100%;
          bottom: 0.5rem;
          h2 {
            line-height: 1.5;
            color: v.$white;
            font-size: 1.75rem;
            span {
              color: v.$primaryBlueDark;
              margin-top: 1rem;
            }
            @include m.laptop {
              font-size: 2rem;
            }
            @include m.desktop {
              font-size: 2.75rem;
            }
            @include m.desktopXL {
              font-size: 3.5rem;
            }
          }
          @include m.desktop {
            bottom: -1rem;
          }
        }
        @include m.desktop {
          height: 4rem;
        }
        @include m.desktop {
          height: 5rem;
        }
      }
      .esthetics-options-treatments {
        margin-top: 5rem;
        .esthetics-options-bg {
          background-color: v.$primaryBlueDark;
        }
        @include m.tablet {
          margin-top: 0;
        }
      }
      @include m.mobile {
        margin-right: 5rem;
        margin-left: 5rem;
      }
      @include m.tablet {
        grid-template-columns: repeat(2, 1fr);
        margin-left: 10%;
        margin-right: 10%;
        gap: 5rem;
      }
      @include m.laptop {
        margin-top: 5rem;
        margin-bottom: 5rem;
        margin-left: 15%;
        margin-right: 15%;
        gap: 10rem;
      }
      & > * {
        transition: transform 0.5s;
        &:hover {
          transform: scale(1.05);
          transition: transform 0.5s;
        }
      }
    }
    @include m.desktop {
      margin-bottom: 20rem;
    }
  }